/* basic css */
* {
  margin : 0;
  padding : 0;
  box-sizing: border-box;
}

html{ 
  font-size: 62.5%;

}

body{
  background: rgb(237, 237, 241);
  color: rgb(14, 14, 15);
  font-family: 'poppins', sans-serif;
}

.App{
  min-height: 100vh;
  display: flex;
  background: #FFFFFF;


}

/* sidebar */
.sidebar{
  flex: 3;
  border-right: 1px solid rgb(255, 255, 255);
}


.logo{
  margin-right: 1rem;
  width: 40px; /* 원하는 너비로 조절 */
  height: 40px; /* 원하는 높이로 조절 */
}

.brand{
  font-size: 2rem;
}

.upperSide{
  padding: 2.5rem; 
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  height : 70%;
}

.upperSideTop{
  display: flex;
  align-items: center;
  margin-bottom: 2.5rem;
}

.addBtn{
  height: 2rem;
  padding-right: 1rem;
}
.midBtn{
  background:#7DE5AE ;
  border: none;
  color: white;
  padding: 1.5rem;
  font-size: 1.5rem; 
  width: 25rem;
  display: flex;
  align-items: center;
  justify-self: center;
  margin :0 auto;
  margin-bottom: 3rem;
  border-radius: 0.5rem;
}

.query{
  display: flex;
  align-items: center;
  background: transparent;
  color: rgba(222, 222, 222, 0.5);
  padding: 1.5rem;
  width: 25rem;
  border-radius: .5rem;
  border: 1px solid rgba(255, 255, 255, 0.1);
}

/* query 안에 있는 이미지 */
.query>img{
  margin-right: 2rem;
  object-fit: cover;
  height: 1.75rem;
}

.lowerSide{
  padding: 2rem;
}

.listItems{
  margin: 1rem;
  display: flex;
  align-items: center;
  font-size: 1.3rem;
}

.listItemsImg{
  margin: 1rem;
  padding-right: 1rem;
  width: 40px;
  height: 30px; 
}

/* lower side bar alignning */
.listItems:nth-child(1) > .listItemsImg{
  margin-right: .5rem;
}
.listItems:nth-child(3) > .listItemsImg{
  margin-right: .75rem;
}

/* main page */
.main{
  flex: 9;
  min-height: calc(100vh - 14rem);
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 6rem 10rem;
}

.chats{
  overflow: hidden;
  overflow-y: scroll;
  scroll-behavior: smooth;
  width: 100%;  
  max-width: 70rem;
  height: calc(100vh - 17rem);
  /* 채팅버블 밑에서 부터 시작 */
  display: flex;
  flex-direction: column; 
  
}

.chat {
  margin: 1rem;
  padding: 2rem 3rem;
  font-size: 1.2rem;
  display: flex;
  align-items: flex-start;
  text-align: justify;
  background-color: #FAFAF6 ;
  color:black;

}

.chatImg{
  object-fit: cover;
  width: 3.5rem;
  margin-right: 2rem ;
  border-radius: 0.5rem;
}

.bot{
  background: #D9FAFF;
  width: 100wh;
  border-radius: 0.5rem;

}
.chatFooter{
  margin-top: auto;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}



.inp{
  padding: 0.5rem;
  background: #FAFAF6;
  display: flex;
  align-items: center;
  border-radius: 0.5rem;
  width: 70rem;
}

.inp > input{
  width: calc (100% - 3rem);
  outline: none;
  padding: 1.25rem;
  color: white;
}

.inp > input, .send{
  padding: 1rem;
  background: transparent;
  border: none;
  color: black;
  font-size: 1.5rem;
}

.txt{
  line-height: 2rem;
  color:  #00204A;
}

.folderlist{
  background: #4be4ff;
  border: none;
  color: white;
  padding: 1.5rem;
  width: 25rem;
  display: flex;
  margin: 0 auto;
  margin-bottom: 5px;
  border-radius: 0.5rem;
}

.folderBtn{
  background: #4be4ff;
  border: none;
  color: white;
  font-size: 1.5rem;
  width: 20rem;
  align-items: left; /* Corrected */
  justify-self: flex-end;
  text-align: left;
/* Corrected */
}

.editBtn{
  padding-right: 1rem;

}


/* modal css */

/* Modal styles */
.modal {
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
}

.modal-content {
  background-color: #fff;
  padding: 20px;
  border-radius: 8px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.3);
  width: 300px;
}

.close {
  position: absolute;
  top: 10px;
  right: 10px;
  cursor: pointer;
}

/* Input and button styles inside the modal */
label {
  display: block;
  margin-bottom: 10px;
}

input {
  width: 100%;
  padding: 8px;
  margin-bottom: 10px;
}

button {
  background-color: #4be4ff;
  color: white;
  border: none;
  padding: 10px;
  border-radius: 5px;
  cursor: pointer;
}

